@font-face {
	font-family: 'MiraiFont';
	src: url('../fonts/MiraiFont.eot?asj19g');
	src: url('../fonts/MiraiFont.eot?asj19g#iefix') format('embedded-opentype'),
	     url('../fonts/MiraiFont.ttf?asj19g') format('truetype'),
	     url('../fonts/MiraiFont.woff?asj19g') format('woff'),
	     url('../fonts/MiraiFont.svg?asj19g#MiraiFont') format('svg');
	font-weight: normal;
	font-style: normal;
}

[class^="ma-"], [class*=" ma-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'MiraiFont' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

